import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/department';

const departmentAll = () => {
	return httpClient.get(END_POINT + "/");
}
const departmentById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const departmentAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const departmentUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const departmentDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	departmentAll,
	departmentById,
	departmentAdd,
	departmentUpdate,
	departmentDelete,
}