import { instanceAll } from '@/api/instance'
import { departmentAll } from '@/api/department'
import { documentTypeAll } from '@/api/documentType'
import { statusAll } from '@/api/status'
import { hardcopyLocationAll } from '@/api/hardcopyLocation'
import { categoryAll } from '@/api/category'
import { userAll } from '@/api/user'
