import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/person_in_charge';

const personInChargeAll = () => {
	return httpClient.get(END_POINT + "/");
}
const personInChargeById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const personInChargeAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const personInChargeUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const personInChargeDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	personInChargeAll,
	personInChargeById,
	personInChargeAdd,
	personInChargeUpdate,
	personInChargeDelete,
}