import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/document_permit_name';

const documentPermitNameAll = () => {
	return httpClient.get(END_POINT + "/");
}
const documentPermitNameById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const documentPermitNameByCodeId = (codeId) => {
	return httpClient.get(END_POINT + "/code_id/" + codeId);
}
const documentPermitNameAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const documentPermitNameUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const documentPermitNameDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	documentPermitNameAll,
	documentPermitNameById,
	documentPermitNameByCodeId,
	documentPermitNameAdd,
	documentPermitNameUpdate,
	documentPermitNameDelete,
}