import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/hardcopy_location';

const hardcopyLocationAll = () => {
	return httpClient.get(END_POINT + "/");
}
const hardcopyLocationById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const hardcopyLocationAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const hardcopyLocationUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const hardcopyLocationDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	hardcopyLocationAll,
	hardcopyLocationById,
	hardcopyLocationAdd,
	hardcopyLocationUpdate,
	hardcopyLocationDelete,
}