import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/document_code';

const documentCodeAll = () => {
	return httpClient.get(END_POINT + "/");
}
const documentCodeById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const documentCodeAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const documentCodeUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const documentCodeDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	documentCodeAll,
	documentCodeById,
	documentCodeAdd,
	documentCodeUpdate,
	documentCodeDelete,
}