<template>
	<Dashboard>
		<div>
			<div class="mb-4 text-white">
				<h5>Pengingat Izin</h5>
				<small>Pengingat Izin</small>
			</div>
			
			<div class="card">
				<div class="card-body" v-if="document != null">
					
					<router-link to="/permit"><i class="fas fa-arrow-left"></i> Kembali ke list</router-link>
					<hr>
					
					<table class="table table-bordered">
						<tbody>
							<tr>
								<td width="20%">Kode Dokumen</td>
								<td>{{ document.number }}</td>
							</tr>
							<tr>
								<td>Nama Dokumen</td>
								<td>{{ selectedData(docPermitNames, document.permitId) }}</td>
							</tr>
							<tr>
								<td>Deskripsi Dokumen</td>
								<td>{{ document.description }}</td>
							</tr>
						</tbody>
					</table>
					
					<div class="card-body">
						<div class="row">
							<div class="col-md-8">
								<h5>Pengingat Izin</h5>
								<p>Pengingat Izin dokumen terkait</p>
							</div>
							<div class="col-md-4 text-right">
								<button @click="showAdd" class="btn btn-success"><i class="fas fa-plus"></i> Tambah Pengingat</button>
							</div>
						</div>
						<hr>
						<table class="table table-bordered">
							<thead>
								<tr>
									<th>Waktu</th>
									<th>Penanggung Jawab</th>
									<th>Aksi</th>
								</tr>
							</thead>
							<tbody>
								<tr v-if="schedules.length == 0">
									<td colspan="3" class="text-center">Belum Ada Data</td>
								</tr>
								<tr v-else v-for="(v,i) in schedules" :key="i">
									<td>{{ v.notificationSchedules.time }} {{ v.notificationSchedules.timeUnit }}</td>
									<td>
										<p v-if="!v.notificationRecipients || v.notificationRecipients.length == 0">Belum Ada Penerima</p>
										<p v-else v-for="(v2,i2) in v.notificationRecipients" :key="i2">
											{{ selectedUser(users, v2.personInChargesId) }} - {{ selectedUser(users, v2.permittingId) }}
										</p>
									</td>
									<td>
										<button @click="onEdit(v)" class="btn btn-warning btn-sm mr-1">
											<i class="fas fa-edit"></i>
										</button>
										<button @click="onDelete(v.notificationSchedules.id)" class="btn btn-danger btn-sm">
											<i class="fas fa-trash"></i>
										</button>
									</td>
								</tr>
							</tbody>
						</table>
					</div>
					
				</div>
			</div>
			
			<div class="modal fade" data-backdrop="static" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true">
				<div class="modal-dialog" role="document">
					<div class="modal-content">
						<div class="modal-header">
							<h5 class="modal-title" id="exampleModalLongTitle">Atur Pengingat</h5>
							<button type="button" class="close" data-dismiss="modal" aria-label="Close">
								<span aria-hidden="true">&times;</span>
							</button>
						</div>
						<div class="modal-body">
							
							<div class="form-group">
								<label>Waktu</label>
								<div class="input-group">
									<input type="number" step="1" class="form-control" v-model="model.time">
									<div class="input-group-append">
										<select class="form-control" v-model="model.timeUnit">
											<option value="Hari">Hari</option>
											<option value="Minggu">Minggu</option>
											<option value="Bulan">Bulan</option>
											<option value="Tahun">Tahun</option>
										</select>
									</div>
								</div>
							</div>
							
							<div class="form-group">
								<label>Penerima Notifikasi</label>
								<div v-for="(v,i) in notificationRecipients" :key="i">
									<div class="row mb-1">
										<div class="col-md-10">
											<h4>{{ selectedUser(users, v.personInChargesId) }} - {{ selectedUser(users, v.permittingId) }}</h4>
										</div>
										<div class="col-md-2">
											<button @click="removePic(i)" class="btn btn-sm btn-danger"><i class="fas fa-times-circle"></i></button>
										</div>
									</div>
								</div>
								<button v-if="!state.pic" @click="state.pic = true" class="btn btn-success btn-block">Tambah Penerima Notifikasi</button>
								<div v-if="state.pic">
									<div class="row">
										<div class="col-md-6">
											<label>Penanggung Jawab</label>
											<v-select class="form-control" :options="users" :reduce="pic => pic.value" v-model="pic.personInChargesId">
											</v-select>
										</div>
										<div class="col-md-6">
											<label>Personnel Permitting</label>
											<v-select class="form-control" :options="users" :reduce="pic => pic.value" v-model="pic.permittingId">
											</v-select>
										</div>
									</div>
								</div>
								<div v-if="state.pic" class="row mt-2">
									<div class="col-md-6">
										<button @click="addPic" class="btn btn-success btn-block">Simpan</button>
									</div>
									<div class="col-md-6">
										<button @click="state.pic = false" class="btn btn-danger btn-block">Batal</button>
									</div>
								</div>
							</div>
							
						</div>
						<div class="modal-footer">
							<button type="button" class="btn btn-secondary" data-dismiss="modal">Batal</button>
							<button @click="onSave" type="button" class="btn btn-primary">Simpan</button>
						</div>
					</div>
				</div>
			</div>
			
		</div>
		
	</Dashboard>
</template>

<script type="text/javascript">
	
	import alertMixin from '@/mixins/alert'
	import documentMixin from '@/mixins/document'
	import { documentById } from '@/api/document'
	import { notificationManagerAdd, notificationManagerByType, notificationManagerUpdate, notificationManagerDelete } from '@/api/notifManager'
	
	export default{
		name: 'Report',
		data(){
			return{
				state: {
					pic: false,
					edit: false,
				},
				docPermitNames: [],
				pics: [],
				users: [],
				document: null,
				schedules: [],
				model: {
					id: null,
					reportScheduleId: null,
					time: 0,
					timeUnit: "Hari",
					reminderType: 1,
				},
				pic: {
					notificationScheduleId: null,
					personInChargesId: null,
					permittingId : null,
				},
				notificationRecipients: [],
			}
		},
		props: [
		'id'
		],
		mixins: [
		alertMixin,
		documentMixin,
		],
		async mounted(){
			this.showLoading();
			this.docPermitNames = await this.getDocPermitName();
			this.users = await this.getPersonInCharge();
			this.getDocument();
		},
		methods: {
			async getDocument(){
				this.showLoading();
				this.document = null;
				this.schedules = [];
				this.pics = [];
				this.notificationRecipients = [];
				this.model = {
					id: null,
					reportScheduleId: null,
					time: 0,
					timeUnit: "Hari",
					reminderType: 1,
				}
				this.pic = {
					notificationScheduleId: null,
					personInChargesId: null,
					permittingId : null,
				}
				try{
					let request = await documentById(this.id);
					this.document = request.data[0].document;
					let schedules = [];
					schedules = await this.getNotifications(this.id);
					this.hideLoading();
					this.schedules = schedules;
					console.log(this.schedules);
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showAlert("Gagal Mendapatkan Data", "error");
					if(error.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			async getNotifications(id){
				try{
					let request = await notificationManagerByType(id, 1);
					return request.data?.[0]?.notificationSchedules;
				}catch(e){
					console.log(e);
					return null;
				}
			},
			showAdd(){
				this.state.edit = false;
				this.model = {
					id: null,
					reportScheduleId: null,
					time: 0,
					timeUnit: "Hari",
					reminderType: 1,
				}
				this.model.documentId = this.id;
				this.notificationRecipients = [];
				
				$("#modalAdd").modal("show");
			},
			addPic(){
				if(this.pic.personInChargesId == "" || this.pic.permittingId == ""){
					this.showNotif("Penerima Harus Diisi");
				}else{
					this.notificationRecipients.push({
						personInChargesId: this.pic.personInChargesId,
						permittingId: this.pic.permittingId,
					});
					this.state.pic = false;
					this.pic.personInChargesId = null;
					this.pic.permittingId = null;
				}
			},
			removePic(index){
				this.notificationRecipients.splice(index,1);
			},
			async onSave(){
				this.showLoading();
				
				if(!this.state.edit){
					try{
						let request = await notificationManagerAdd({
							notificationSchedule: this.model,
							notificationRecipients: this.notificationRecipients,
						});
						this.hideLoading();
						$("#modalAdd").modal("hide");
						this.showAlert("Pengingat Berhasil Disimpan");
						this.getDocument();
					}catch(e){
						this.hideLoading();
						console.log(e);
						this.showNotif("Terjadi Kesalahan");
						if(error.response?.status == 401){
							this.invalidSession();
						}
					}
				}else{
					try{
						let notificationRecipients = [];
						this.notificationRecipients.forEach((v,i) => {
							v.notificationScheduleId = this.model.id;
							notificationRecipients.push(v);
						})
						
						let request = await notificationManagerUpdate(this.model.id, {
							notificationSchedule: this.model,
							notificationRecipients: notificationRecipients,
						});
						this.hideLoading();
						$("#modalAdd").modal("hide");
						this.showAlert("Pengingat Berhasil Disimpan");
						this.getDocument();
					}catch(e){
						this.hideLoading();
						console.log(e);
						this.showNotif("Terjadi Kesalahan");
						if(error.response?.status == 401){
							this.invalidSession();
						}
					}
				}
			},
			onEdit(data){
				let doto = data;
				this.model = {
					id: null,
					reportScheduleId: null,
					time: 0,
					timeUnit: "Hari",
					reminderType: 1,
				};
				this.notificationRecipients = [];

				let schedules = doto.notificationSchedules;
				let recipients = doto.notificationRecipients;

				this.model = {
					id: schedules.id,
					reportScheduleId: schedules.reportScheduleId,
					time: schedules.time,
					timeUnit: schedules.timeUnit,
					reminderType: schedules.reminderType,
				};
				recipients.forEach((v) => {
					this.notificationRecipients.push(v);
				});
				
				this.state.edit = true;
				$("#modalAdd").modal("show");
			},
			onDelete(id){
				this.$swal({
					title: 'Hapus Data',
					text: "Yakin ingin menghapus data ?",
					type: 'error',
					showCancelButton: true,
					confirmButtonColor: '#3085d6',
					cancelButtonColor: '#d33',
					confirmButtonText: 'Yes'
				}).then((result) => {
					if (result.value) {
						let request = notificationManagerDelete(id);
						request
						.then((response) => {
							this.hideLoading();
							
							this.$toast.open({
								message: 'Pengingat Berhasil Dihapus !',
								type: 'info',
								position: 'top-right',
							});
							
							this.getDocument();
						})
						.catch((error) => {
							this.hideLoading();
							this.showNotif("Terjadi Kesalahan");
							console.log(error);
							if(error.response?.status == 401){
								this.invalidSession();
							}
						})
					}
				});
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>