import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/sub_instance';

const subInstanceAll = () => {
	return httpClient.get(END_POINT + "/");
}
const subInstanceById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const subInstanceAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const subInstanceUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const subInstanceDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	subInstanceAll,
	subInstanceById,
	subInstanceAdd,
	subInstanceUpdate,
	subInstanceDelete,
}