import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/document_type';

const documentTypeAll = () => {
	return httpClient.get(END_POINT + "/");
}
const documentTypeById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const documentTypeAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const documentTypeUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const documentTypeDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	documentTypeAll,
	documentTypeById,
	documentTypeAdd,
	documentTypeUpdate,
	documentTypeDelete,
}