import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/notification_manager';

const notificationManagerAll = () => {
	return httpClient.get(END_POINT + "/");
}
const notificationManagerByType = (id, type) => {
	return httpClient.get(END_POINT + "/document_id/" + id + "/reminder_type/" + type);
}
const notificationManagerById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const notificationManagerAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const notificationManagerUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const notificationManagerDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}
const notificationManagerByReportId = (id) => {
	return httpClient.get(END_POINT + "/report_schedule_id/" + id);
}

export {
	notificationManagerAll,
	notificationManagerByType,
	notificationManagerById,
	notificationManagerAdd,
	notificationManagerUpdate,
	notificationManagerDelete,
	notificationManagerByReportId,
}